//#region Global Imports
import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { Link } from 'react-router-dom';
// import NEOLink from 'next/link';
//#endregion Global Imports

//#region Interface Imports
// import { Heading } from 'Components/UI/Heading';
import { Store } from 'Components/Stores/IStore';
import { IHomePage } from './IHomePage';
import { HomeActions } from './HomeActions';
// import { CreateTicketPage } from 'Pages/CustomerCarePage/CreateTicketPage/CreateTicketPage';
// import { Route, Router } from 'react-router';
import { Heading } from 'Components/UI/Heading';
import { API } from 'Pages/CustomerCarePage/API';

//#endregion Interface Imports

export class HomePage extends React.Component<IHomePage.IProps, IHomePage.IState> {
	constructor(props: IHomePage.IProps) {
		super(props);

		this.state = {
			tickets: []
		}
	}

	componentDidMount() {
		API.get('customer-care/tickets',
            (response) => {
                this.setState({tickets: response});
            },
            (error) => {
                console.log(error);
            });
	}

	public render(): JSX.Element {
		return (
			<div>
				<div className='title'>
					<Heading text="Home page"/>
					<Link to='/customer-care/create-ticket'>Create Ticket</Link>
					<Link to="/customer-care/tickets/list">Tickets List</Link>
					{/* <Link to='/customer-care/update-ticket/83012'>Update Ticket</Link> */}
				</div>
				<br />
				<div>{"TICKETS Counter: " + this.state.tickets.length}</div>
				<br />
				<div>{'Click on ticket you wish to update'}</div>
				<div>{'(Only Inpatient tickets for now)'}</div>
				{this.state.tickets
					.filter(ticket => ticket.ticket_category.confinement_details)
					.map(ticket => {
						return (
							<div>
								<Link to={'/customer-care/update-ticket/' + ticket._id}>{'Ticket ID: ' + ticket._id}</Link>
							</div>
						);
					})}
				<br />
				<br />
				<br />
				<br />
				<br />
				<div>{'Click on ticket you wish to resolve'}</div>
				<div>{'(Only Outpatient tickets for now)'}</div>
				{this.state.tickets
					.filter(ticket => ticket.ticket_category.availment_details)
					.map(ticket => {
						return (
							<div>
								<Link to={'/customer-care/resolve-ticket/' + ticket._id}>
									{'Ticket ID: ' + ticket._id + '(' + ticket.status + ')'}
								</Link>
							</div>
						);
					})}
				<br />
				<br />
				<br />
				<br />
				<br />
				<div>{'Click on ticket you wish to update'}</div>
				<div>{'(Inquiry Tickets)'}</div>
				{this.state.tickets
					.filter(ticket => ticket.ticket_category.inquiry_details)
					.map(ticket => {
						return (
							<div>
								<Link to={'/customer-care/update-ticket/' + ticket._id}>
									{'Ticket ID: ' + ticket._id + '(' + ticket.status + ')'}
								</Link>
							</div>
						);
					})}
				<br />
				<br />
				<br />
				<br />
				<br />
				<div>{'Click on ticket you wish to update'}</div>
				<div>{'(Consultation Tickets)'}</div>
				{this.state.tickets
					.filter(ticket => ticket.ticket_category.consultation_details)
					.map(ticket => {
						return (
							<div>
								<Link to={'/customer-care/update-ticket/' + ticket._id}>
									{'Ticket ID: ' + ticket._id + '(' + ticket.status + ')'}
								</Link>
							</div>
						);
					})}


			</div>
		);
	}
}

const mapStateToProps = (state: Store) => state.home;

const mapDispatchToProps = (dispatch: Dispatch) => ({
  Map: bindActionCreators(HomeActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
